.testimonials {
	background-color: #672f1b;

	position: relative;

	&::after {
		@include pseudo;
		@include pos-abs ($t: -80px, $r: 60px);
		@include box-size (410px, 301px);
		@include background (url("../images/testimonials/testimonials_dots.png"));

		@media only screen and (max-width: 1440px) {
			display: none;
		}
	}

	&__inner {
		@include mar (0, auto, 0, auto);
		@include pad (116px, 0, 80px, 0);
	}

	&__title {
		@include mar (0, 0, 130px, 0);
		text-align: center;

		&--big {
			text-transform: uppercase;
			color: $text-cards-color;
		}
	}

	&__box {
		@include flex (center, center);
		@include mar (0, auto, 120px, auto);
		position: relative;

		&::after {
			@include pseudo ('Sarah A');
			@include pos-abs ($t: 15px, $l: 41%);
			@include font ($fs: 12.5rem, $lh: 0.2em, $ls: -0.02em, $fw: 800);
			@include transform (translateX(-50%));
			opacity: 0.6;
			color: #521905;

			@media only screen and (max-width: 1440px) {
				display: none;
			}
		}

	}

	&__textbox {
		@include flex (center, center, wrap, column);
		@include mar (35px, 50px, 0, 0);
		max-width: 460px;

		@media only screen and (max-width: 768px) {
			@include mar (80px, 0, 0, 0);
		}
	}

	&__quote {
		@include font ($fs: 1.1rem, $lh: 1.95em);
		@include mar (0, 0, 10px, 0);
		text-align: center;

		z-index: 1;

		@media only screen and (max-width: 768px) {
			@include mar (0, 0, 0, 0);
		}
	}

	&__cite {
		@include font ($fs: 1.1rem, $lh: 1.95em, $fw: 600);
		font-style: italic;
		text-align: center;
		color: #f7ae3b;
	}

	&__img {
		z-index: 1;
	}

	&__container {
		@include flex (center, center);
		@include mar (0, auto, 0, auto);
		max-width: 160px;
		/* 160px */
	}

	&__btn {
		@include background (url("../images/base/arrow.png"));
		@include box-size (30px, 16px);
		display: block;

		&:first-child {
			@include mar (0, 100px, 0, 0);
		}
	}

}